import React, { Component } from "react";
import Slider from "react-slick";
import { Link } from "react-router-dom";
export default class Model extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 6,
      slidesToScroll: 2,
      lazyLoad: true,
      className: ".slick-prev .slick-next",
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 2,
            infinite: true
          }
        },
        {
          breakpoint: 900,
          settings: {
            slidesToShow: 5,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 520,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 350,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 310,
          settings: {
            slidesToShow: 1
          }
        }
      ]
    };
    return (
      <div>
        <Slider {...settings}>
          {this.props.model.map(result => {
            let div = (
              <div className="card" style={{ width: 25 }} key={result._id}>
                <Link to={{ pathname: "/model", state: { model: result } }}>
                  <img
                    src={result.thumb}
                    alt={result.name}
                    className="card-img-top"
                  />
                </Link>
                <div className="card-body">
                  <h6 className="card-title text-center">{result.name}</h6>
                </div>
              </div>
            );
            return div;
          })}
        </Slider>
      </div>
    );
  }
}
