import React, { Component } from "react";
import Model from "./model/Model";
const animals = require("../3dmodelDB/animals.json");
const foods = require("../3dmodelDB/foods.json");
const furnitures = require("../3dmodelDB/furnitures.json");
const natures = require("../3dmodelDB/natures.json");
const technologies = require("../3dmodelDB/technologies.json");
const transports = require("../3dmodelDB/transports.json");
class Models extends Component {
  constructor(props) {
    super(props);
    this.state = {
      models: [animals, foods, furnitures, natures, technologies, transports]
      // animals: animals,
      // foods: foods,
      // furnitures: furnitures,
      // natures: natures,
      // technologies: technologies,
      // transports: transports
    };
  }
  // componentWillMount() {}
  render() {
    let count = 0;
    return (
      <div>
        {this.state.models.map(result => {
          return (
            <div className="heading" key={++count}>
              <h4>{result[0].category}</h4>
              <Model model={result} />
            </div>
          );
        })}
      </div>
    );
  }
}

export default Models;
