import React, { Component } from "react";
// import { Link } from "react-router-dom";
import "aframe";
export default class Aframe extends Component {
  render() {
    return (
      <div>
        {/* <h4>Hello World</h4> */}
        {/* <Link to="/">
          <button>Back</button>
        </Link> */}
        <div>
          <a-scene background="color:whitesmoke">
            <a-assets>
              <a-asset-item
                id="obj"
                src={this.props.location.state.model.obj}
              />
              <a-asset-item
                id="mtl"
                src={this.props.location.state.model.mtl}
              />
            </a-assets>
            <a-camera
              position={this.props.location.state.model.camera.position}
              rotation={this.props.location.state.model.camera.rotation}
            />
            <a-entity>
              <a-entity
                position={this.props.location.state.model.entity.position}
                rotation={this.props.location.state.model.entity.rotation}
                scale={this.props.location.state.model.entity.scale}
                obj-model="obj:#obj;mtl:#mtl"
              />
              <a-animation
                attribute="rotation"
                dur="4000"
                from={this.props.location.state.model.entity.rotation}
                to="0 360 0"
                repeat="indefinite"
              />
            </a-entity>
          </a-scene>
        </div>
      </div>
    );
  }
}
